.App {
  text-align: center;
}

.App-logo {
  height: 20%;
  width: auto;
  pointer-events: none;
}

.Resize {
  height: 30%;
  width: auto;
  pointer-events: none;
}

.Jevil1 {
  color: "#333333";
  background: url("/anything.png") no-repeat scroll 0 0 transparent;
  height: 1000px;
}

.Jevil2 {
  color: "#333333";
  background: url("/anything.png") no-repeat scroll 0 0 transparent;
}


#Applink {
  margin-top: 132px;
  font-size: 60px;
  font-weight: 550;
  color: #909090;
  text-decoration: none;
  margin-bottom: 250px
}

#Applink:hover {
  color: #DDDDDD;
}

.App-link {
  color: #909090;
}

.App-link:hover {
  color: #DDDDDD;
}

@media (prefers-reduced-motion: no-preference) {
  .Jevil0 {
    animation: App-logo-spin0 infinite 1s linear;
    display: flex;
    margin-top: -600px;
    margin-left: -420px;
    align-items: center;
    height: 1000px;
    width: auto;
  }
  .Jevil1 {
    animation: App-logo-spin0 infinite 1s linear;
    display: grid;
    margin-top: -1050px;
    margin-left: 1100px;
    align-items: center;
    height: 1000px;
    width: auto;
  }
  .Jevil2 {
    animation: App-logo-spin0 infinite 1s linear;
    margin-top: -250px;
    margin-left: 1100px;
    align-items: center;
    height: 1000px;
    width: auto;
  }
}

.App-header {
  background-color: #730073;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin0 {
  10% {
    transform: rotate(20deg);
  }
  25% {
    transform: rotate(-20deg);
  }
  50% {
    transform: rotate(0deg);
  }
  60% {
    transform: rotate(20deg);
  }
  75% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
